import React, { useEffect, useState } from 'react';
import defaultData from '../defaultData';

const images: React.FC[] = [];
images.push(require('../../images/service-1.svg'));
images.push(require('../../images/service-2.svg'));
images.push(require('../../images/service-3.svg'));
images.push(require('../../images/service-4.svg'));
images.push(require('../../images/service-5.svg'));

interface ServiceProps {
  data: any[];
}

interface ServiceItemProps {
  className: string;
  image: string;
  title: string;
  color: string;
  description: string;
}

const Service: React.FC<ServiceProps> = props => {
  const [clickedMoreButton, setClickedMoreButton] = useState(false);

  const handleClickMoreButton = () => {
    setClickedMoreButton(true);
  };

  const ServiceItem = (props: ServiceItemProps) => (
    <div className={`service-item m-3 p-3 ${props.className}`}>
      <img
        className='d-block mx-auto my-0'
        src={props.image}
        alt={props.title}
      />
      <h2 className='h5 text-center my-4' style={{ color: props.color }}>
        {props.title}
      </h2>
      <p>{props.description}</p>
    </div>
  );

  return (
    <section id='service' className='bg-white'>
      <div className='container'>
        <div className='mb-5 text-center'>
          <h2 className='mb-1'>{defaultData.sections.service.title}</h2>
          <p>{defaultData.sections.service.title_ja}</p>
        </div>
        <div className='service-background'>
          <div className='row row-cols-1 row-cols-lg-2'>
            <div className='col-lg-4'>
              {props.data && props.data[0] && (
                <ServiceItem
                  className='service-item-1'
                  image={props.data[0].image.url}
                  title={props.data[0].title}
                  color={props.data[0].color}
                  description={props.data[0].description}
                />
              )}
            </div>
            <div
              className={`col-lg-4 ${
                clickedMoreButton ? 'd-none' : 'd-sm-none'
              }`}
            >
              <div className='service-morebutton m-3'>
                <button
                  className='w-100 btn btn-outline-secondary'
                  onClick={handleClickMoreButton}
                  type='button'
                >
                  ▼続きを読む
                </button>
              </div>
            </div>
            {props.data &&
              props.data.map((d: any, i: number) => {
                // 1番目の要素だけ特殊なためループ外で定義済
                if (i === 0) return;
                return (
                  <div
                    key={i}
                    className={
                      (i === 3 ? 'col-lg-4 offset-lg-2' : 'col-lg-4') +
                      (clickedMoreButton ? '' : ' d-none d-sm-block')
                    }
                  >
                    <ServiceItem
                      className={`service-item-${i + 1}`}
                      image={d.image.url}
                      title={d.title}
                      color={d.color}
                      description={d.description}
                    />
                  </div>
                );
              })}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Service;
